@import "./components/colors.scss";

@font-face {
  font-family: "RussoOne";
  src: url("./assets/RussoOne-Regular.ttf") format("truetype");
}

@font-face {
  font-family: "EudoxusBold";
  src: url("./assets/EudoxusSans-Bold.ttf") format("truetype");
}

@font-face {
  font-family: "EudoxusRegular";
  src: url("./assets/EudoxusSans-Regular.ttf") format("truetype");
}

.App {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  width: 700px;
  margin-top: 17.5%;
}

.search-box {
  display: flex;
  position: relative;
  flex-direction: column;
  justify-content: space-between;
  position: relative;
  align-self: center;
  align-items: center;
  background: linear-gradient(120deg, $primary-color -35%, rgba($secondary-color, 0.5) 70%) padding-box;
  width: 100%;
  height: fit-content;
  margin-bottom: 16px;
  padding: 32px;
  border: 1px solid transparent;
  border-radius: 16px;
  box-shadow: 0 0 16px black;
  transition: height 0.2s ease-in-out;
  backdrop-filter: blur(10px);

  &__header {
    display: flex;
    flex-direction: row-reverse;
    width: 100%;
    justify-content: center;
    align-items: center;
    z-index: 3;
  }

  &__buttons-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 8px;
    z-index: 3;

    span {
      text-align: center;
      font-size: 0.8rem;
      color: rgba($light-font-color, 0.7);
      text-shadow: 0 0 4px black;
      margin-bottom: 32px;
    }
  }

  &__input-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 100%;
    gap: 16px;
    z-index: 3;
  }

  &__input-text {
    font-family: "EudoxusRegular", "sans-serif";
    font-size: 1.2rem;
    font-weight: 600;
    border-radius: 12px;
    outline: none;
    border: none;
    padding: 16px;
    z-index: 3;
  }

  &__input-text::placeholder {
    font-family: "EudoxusRegular", "sans-serif";
    font-style: italic;
  }

  &__input-text-disabled {
    height: 32px;
    border-radius: 12px;
    outline: none;
    border: none;
    padding: 16px;
    font-size: 1.2rem;
  }

  &__button {
    width: 70%;
    border: 1px solid transparent;
    border-radius: 16px;
    font-family: "EudoxusBold", "sans-serif";
    font-size: 1.2rem;
    padding: 16px;
    background: linear-gradient(130deg, $accent-color 0%, $dark-accent-color 100%) padding-box,
      linear-gradient(130deg, #ff459f 20%, transparent 100%) border-box;
    box-shadow: 0 0 4px black;
    color: white;
    cursor: pointer;
    z-index: 3;
  }

  &__button:disabled {
    cursor: wait;
    border: none;
    border-radius: 16px;
    font-size: 0.9rem;
    font-weight: 600;
    padding: 16px;
    background: $secondary-color;
    color: $primary-color;
  }
}

.results-list {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  padding: 0;
  gap: 16px;
}

.spotify-login-button {
  background: $spotify-green;
  justify-self: flex-end;
  border: none;
  max-width: 200px;
  padding: 12px;
  border-radius: 50px;
  color: $dark-font-color;
  font-family: "EudoxusRegular", "sans-serif";
  font-size: 1rem;
  font-weight: 600;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.5);
  z-index: 6;
}

.spotify-login-button:active {
  box-shadow: 0 0 0 0;
  position: relative;
  top: 4px;
}

.user-name {
  color: $light-font-color;
  align-self: flex-start;
  font-weight: 400;
  margin: 0;
  padding: 0;
}

.tools-container {
  display: flex;
  width: 100%;
  margin: 0;
  margin-bottom: 32px;
  padding: 0;
  z-index: 3;

  .setting-container {
    display: flex;
    align-items: center;
    width: 100%;
    margin: 0;
    padding: 0;
    z-index: 3;

    &__input {
      width: 80px;
      font-family: "EudoxusRegular", "sans-serif";
      font-size: 1.2rem;
      font-weight: 400;
      height: 16px;
      border-radius: 32px;
      outline: none;
      border: none;
      padding: 16px;
      z-index: 3;
    }

    &__input::placeholder {
      font-family: "EudoxusRegular", "sans-serif";
      font-size: 1rem;
      padding-left: 8px;
    }
  }
}

.select-genres-button {
  border: 1px solid transparent;
  border-radius: 12px;
  font-family: "EudoxusBold", "sans-serif";
  font-size: 0.8rem;
  text-transform: uppercase;
  padding: 12px;
  background: linear-gradient(130deg, $accent-color 0%, $dark-accent-color 100%) padding-box,
    linear-gradient(130deg, #ff459f 20%, transparent 100%) border-box;
  box-shadow: 0 0 4px black;
  color: white;
  cursor: pointer;
  z-index: 3;
}
